rollo Sostenible (ODS), y de manera principal con los de “Energía asequible y no contaminante”, “Ciudades y comunidades sostenibles” y “Producción y consumo responsables”, requiriendo personas con formación pertinente para su efectiva realización. ")]),_c('p',[_vm._v("A nivel nacional, las disposiciones de las leyes 697 de 2001 y 1715 de 2014, establecen como un asunto de interés social, público y de conveniencia nacional el Uso Racional y Eficiente de la Energía (URE), así como también la integración de las energías renovables no convencionales y el desarrollo y promoción de la gestión eficiente de la energía, siendo implementadas bajo el Programa de Uso Racional y eficiente de la energía - PROURE, con los planes adoptados al efecto, el cual establece a “La educación, innovación y desarrollo tecnológico” como una de las estrategias y acciones base para para el cumplimiento de las metas sectoriales. En el Plan Energético Nacional (PEN) 2020 – 2050 se considera como uno de sus propósitos “(…) definir un modelo energético sostenible al 2050, que impulse la transformación energética para lograr el progreso económico, el mejoramiento de la calidad de vida y el respeto al medio ambiente.”. ")]),_c('p',[_vm._v("Colombia, para la implementación de los ODS considera en su agenda 2030, entre otras metas, las de duplicar la tasa de mejora de la eficiencia energética; aumentar la cooperación internacional para facilitar el acceso a la investigación y tecnologías sobre la eficiencia energética, avanzadas y menos contaminantes; así como ampliar la infraestructura y mejorar la tecnología para prestar servicios energéticos modernos y sostenibles. En el mismo sentido, se encuentran las metas de reducción del impacto ambiental negativo de las ciudades; el apoyo mediante asistencia financiera y técnica para la construcción de edificios sostenibles y resilientes, así como la ampliación de actores y empresas que adoptan e implementan políticas y planes integrados para lograr la gestión sostenible y el uso eficiente de los recursos naturales con la adopción de prácticas sostenibles sobre reducción de residuos e incorporación de información sobre la sostenibilidad de sus ciclos. ")]),_c('p',[_vm._v("El programa “Tecnología en Gestión Eficiente de la Energía”, dirigido a personas con conciencia ambiental y habilidades digitales, facilitará la implementación de metas dispuestas para el logro de los ODS. El campo de acción del egresado del programa se extiende a organizaciones públicas y privadas de diversos sectores económicos, tales como transporte, industrial, terciario, residencial, minero y agrícola, donde la gestión del consumo energético de diferentes fuentes, tales como gas, carbón, gasolina y energías renovables no convencionales, es visto de manera integrada a los ciclos productivos, facilitando la realización de diagnósticos, la elaboración y realización de planes de acción que redundan en mejoramiento de la productividad y la sostenibilidad de las organizaciones. Así mismo, la formación integral que brinda el programa fomenta la inclusión social, capacidades creativas y emprendedoras en el egresado. ")]),_c('p',[_vm._v("La modalidad virtual del programa, lo hace asequible a personas que pueden estar localizadas en zonas apartadas, con lo cual a la vez que garantiza la disponibilidad, potencializa la reducción de las brechas de género y con ello se da cabida al acceso igualitario a la educación y a las carreras STEM (Science, Technology Engineering and Mathematics), las cuales apuntalan el Desarrollo Sostenible mediante la creatividad y la innovación. ")]),_c('p',[_vm._v("El SENA con este programa de formación en el marco del “Pacto por los Empleos Verdes y Transición Justa en Colombia”, contribuye a la promoción de empleos que atienden las demandas de nuevas capacidades laborales para la adaptación del sector productivo, a los retos de productividad laboral y del crecimiento verde. ")]),_c('p',{staticClass:"mb-0"},[_vm._v("La oferta de pro